import React from 'react';
import { Container, Col, Card, CardDeck} from 'react-bootstrap'; 
import hatha from '../Images/hatha.jpg'
import ashtanga from '../Images/ashtanga.png'
import vinyasa from '../Images/vinyasa.png'
import bikram from '../Images/bikram.png'
import kundalini from '../Images/kundalini.png'
import iyengar from '../Images/iyengar.png'

const Lessons = () => (
  <p>
    <Container className="vignettes" fluid >
   
<CardDeck>

  <Col xs={12} md={4}>
<Card >
  <Card.Img variant="top" src={ashtanga}/>
  <Card.Body>
    <Card.Title>Ashtanga</Card.Title>
    <Card.Text>
      Yoga dynamique et trs codifi.
      En Asthanga on pratique des sries contenant un certain nombre de postures diffrentes.
      Dans les cours dAshtanga, cest cardio et il nest pas rare de transpirer !
    </Card.Text>
  </Card.Body>
</Card>
</Col>

<Col xs={12} md={4}>
<Card >
  <Card.Img variant="top" src={vinyasa}/>
  <Card.Body>
    <Card.Title>Vinyasa</Card.Title>
    <Card.Text>
    le Vinyasa est proche de lAshtanga, mais il offre une plus grande libert dans lenchanement des postures. De plus, chaque posture est suivie dun vinyasa, cest--dire un enchanement de postures prcis, qui relie chaque posture.
    </Card.Text>
  </Card.Body>
</Card>
</Col>

<Col xs={12} md={4}>
<Card className="mb-4">
  <Card.Img variant="top" src={hatha}/>
  <Card.Body>
    <Card.Title>Hatha</Card.Title>
    <Card.Text>
   Yoga doux qui consiste  conserver longtemps chaque posture et donc  solliciter les muscles et la concentration en profondeur. On enchane les postures les unes aprs les autres, sans raliser de transitions particulires entre chaque posture.
    </Card.Text>
  </Card.Body>
</Card>
</Col>

<Col xs={12} md={4}>
<Card>
  <Card.Img variant="top" src={bikram}/>
  <Card.Body>
    <Card.Title>Bikram</Card.Title>
    <Card.Text>
    Lorsque vous entrez dans un studio de yoga bikram, la premire chose que vous remarquerez est la chaleur. Chaque studio est chauff  environ 105 degrs (F) et prsente un taux d'humidit de 40 pour cent. Durant le cours de 90 minutes, vous pratiquerez un ensemble de 26 poses, comme la pose du bton en quilibre et celle de la sauterelle, ainsi que deux exercices de respiration qui, selon le fondateur Bikram Choudhury, permettent de faire systmatiquement circuler le sang oxygn dans le corps.
    </Card.Text>
  </Card.Body>
</Card>
</Col>

<Col xs={12} md={4}>
<Card>
  <Card.Img variant="top" src={iyengar}/>
  <Card.Body>
    <Card.Title>Iyengar</Card.Title>
    <Card.Text>
    Le yoga Iyengar a pour objectif de porter une attention prcise  notre alignement durant chaque posture. Vous utiliserez une varit d'accessoires tels que des blocs, des couvertures, des sangles, des chaises pliantes et mme des murs de cordes afin de trouver le bon alignement pour chaque position. Plutt que de passer d'une pose  l'autre rapidement, vous devrez tenir chaque posture pour une plus longue priode. Les cours durent habituellement une heure et demie. Le yoga Iyengar peut tre thrapeutique pour les gens qui rcuprent de certaines blessures.
    </Card.Text>
  </Card.Body>
</Card>

</Col>
<Col xs={12} md={4}>
<Card>
  <Card.Img variant="top" src={kundalini}/>
  <Card.Body>
    <Card.Title>Kundalini</Card.Title>
    <Card.Text>
    Si vous cherchez une pratique plus spirituelle et base sur l'nergie, essayez le kundalini, fond par le yogi Bhajan. Le kundalini est compos de kriyas, des exercices rptitifs combins  une respiration intense (comme si vous respiriez du feu); cette pratique peut tre exigeante et nergisante sur le plan physique. Grce  des mouvements,  votre respiration et  des sons (psalmodie), vous commencerez  stimuler et  relcher de l'nergie non exploite au niveau de votre colonne vertbrale et par l'entremise de vos centres d'nergie, aussi appels chakras. La dure des classes est habituellement d'une heure ou plus.
    </Card.Text>
  </Card.Body>
</Card>
</Col>

</CardDeck>
</Container>
  </p>
)

export default Lessons;