import React from 'react'
import { Container, Col, Card, CardDeck} from 'react-bootstrap'; 

import ashtanga from '../Images/ashtanga.png'
import vinyasa from '../Images/vinyasa.png'

const YogaDyn = () => (
    <p>
      <Container className="vignettes" fluid >
          <CardDeck>

<Col xs={12} md={6}>
<Card >
  <Card.Img variant="top" src={ashtanga}/>
  <Card.Body>
    <Card.Title>Ashtanga</Card.Title>
    <Card.Text>
      Yoga dynamique et trs codifi.
      En Asthanga on pratique des sries contenant un certain nombre de postures diffrentes.
      Dans les cours dAshtanga, cest cardio et il nest pas rare de transpirer !
    </Card.Text>
  </Card.Body>
</Card>
</Col>

<Col xs={12} md={6}>
<Card >
  <Card.Img variant="top" src={vinyasa}/>
  <Card.Body>
    <Card.Title>Vinyasa</Card.Title>
    <Card.Text>
    le Vinyasa est proche de lAshtanga, mais il offre une plus grande libert dans lenchanement des postures. De plus, chaque posture est suivie dun vinyasa, cest--dire un enchanement de postures prcis, qui relie chaque posture.
    </Card.Text>
  </Card.Body>
</Card>
</Col>


</CardDeck>
</Container> 
</p>

)

export default YogaDyn;